<template>
  <div class="home">
    <div class="pa-4">
      <header 
      :value="computedDateFormattedMomentjs"
      class="list-group-item" 
      v-show="fulldatetime"
      > 
        {{ fulldatetime }}
        <v-icon
          @click="dateReset"
        >mdi-reload</v-icon>
      </header>
    </div>
    
    <div class="px-4">
      <v-menu
        v-model="menu"
        :close-on-content-click="false"
        max-width="290"
      >
        <template v-slot:activator="{ on, attrs }">
          <v-text-field
            :value="computedDateFormattedMomentjs"
            clearable
            label="Pick a date"
            prepend-icon="mdi-calendar"
            v-bind="attrs"
            v-on="on"
            @click:clear="date = ''"
          >
            <template v-slot:append>
              <v-icon
              :disabled="date == ''"
              @click="addDate" 
              >
                mdi-checkbox-marked
              </v-icon>
            </template>  
          </v-text-field>  
        </template>
        <v-date-picker
          v-model="date"
          @change="menu = false"
        ></v-date-picker>
      </v-menu>
    </div>

    <v-text-field
      v-model="newTaskTitle"
      @keyup.enter="addTask"
      class="px-4"
      outlined
      label="Add a Task"
      clearable
    >
      <template v-slot:append>
        <v-icon
        :disabled="newTaskTitle == ''"
        @click="addTask" 
        >
          mdi-plus
        </v-icon>
      </template>  
    </v-text-field>

    <v-list
      class="px-4"
      flat
    >
      <div
        v-for="task in tasks"
        :key="task.id"
      >
        <v-list-item
          @click="doneTask(task.id)"
          :class="{'blue lighten-5': task.done}"
        >
          <template v-slot:default>
            <v-list-item-action>
              <v-checkbox
                :input-value="task.done"
                color="primary"
              ></v-checkbox>
            </v-list-item-action>
          
            <v-list-item-content>
              <!-- Change task.title to value function called that gets task based on date stored -->
              <v-list-item-title
                :class="{'text-decoration-line-through':task.done}"
              >{{task.title}}</v-list-item-title>
              
            </v-list-item-content>

            <v-list-item-action>
              <v-btn
                @click.stop="deleteTask(task.id)"
                icon
              >
                <v-icon color="red">mdi-delete</v-icon>
              </v-btn>
            </v-list-item-action>
          </template>
        </v-list-item>
        <v-divider></v-divider>
      </div>
      
    </v-list>
  </div>  
</template>

<script>
import moment from 'moment'

  export default {
    name: 'Home',
    data() {
      return {
        fulldatetime: '',
        newTaskTitle: '',
        tasks: [
        ],
        activePicker: null,
        date: '',
        menu: false,
      }
    },

    computed: {
      computedDateFormattedMomentjs () {
        return this.date ? moment(this.date).format('dddd, MMMM Do YYYY') : ''
      },
    },
    
    methods: {
      dateReset(){
        // return all the task data for today
        var d = new Date();
        this.fulldatetime=d ? moment(d).format('dddd, MMMM Do YYYY') : ''
      },

      addDate(){
        // return all the task data for this day
        this.fulldatetime=this.date ? moment(this.date).format('dddd, MMMM Do YYYY') : ''
      },
      addTask() {
        let newTask = {
          id: Date.now(),
          title: this.newTaskTitle,
          done: false
        }
        //put value to database
        this.tasks.push(newTask)
        this.newTaskTitle = ''
      },
      doneTask(id){
        let task = this.tasks.filter(task => task.id === id)[0]
        task.done = !task.done
      },
      deleteTask(id){
        //make a deletion in database
        this.tasks = this.tasks.filter(task => task.id  !== id)
      },
      printFullDate: function () {
        //put value to database
        var d = new Date();
        var date = d ? moment(d).format('dddd, MMMM Do YYYY') : ''
        return date
      },
      
    },
    mounted: function () {
      this.fulldatetime = this.printFullDate();
    },
  }
</script>

<style>
   .list-group-item{
     font-size:25px;
     font-weight:bold;
     font-family: "Calibri";
   }
</style>
